<template>
  <div class="video-wrapper">
    <button @click="getUserMediaStream"></button>
    <video id="rtcA" ref="videoA" src="" preload="auto" />
    <video id="rtcB" ref="videoB" src="" preload="auto" />
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    return {
      peerA: null,
      peerB: null,
      offerOption: ''
    }
  },
  mounted () {
    // this.getUserMediaStream()
  },
  methods: {
    // 
    getUserMediaStream () {
      const constraints = {
        video: true,
        audio: true
      }

      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {}
      }
      // mediaDevices
      // getUserMedia
      // getUserMedia
      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          // getUserMedia()
          var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.getUserMedia
          // 
          // 
          if (!getUserMedia) {
            return Promise.reject(new Error('getUserMedia is not implemented in this browser'))
          }
          // Promisenavigator.getUserMedia
          return new Promise(function (resolve, reject) {
            getUserMedia.call(navigator, constraints, resolve, reject)
          })
        }
      }

      navigator.mediaDevices.getUserMedia(constraints)
        .then((stream) => {
          const video = document.querySelector('#rtcA')
          console.log('stream', stream.length, stream)

          // video.srcObject = window.URL.createObjectURL(stream)
          video.src = window.URL.createObjectURL(stream) || stream
          video.onloadedmetadata = function (e) {
            console.log('')
            video.play()
          }
        })
        .catch((err) => {
          console.error(err)
        })
      // return new Promise((resolve, reject) => {
      //   getUserMedia.call(navigator, {
      //     audio: true,
      //     video: true
      //   }, (stream) => {
      //     console.log('stream', stream)
      //     // video
      //     this.localstream = stream
      //     const video = this.$refs.videoA
      //     video.srcObject = stream
      //     video.src = stream
      //     video.play()
      //     resolve()
      //   }, function (err) {
      //     reject(err)
      //   })
      // })
    },

    //  RTCPeerConnection
    initPeer () {
      const PeerConnection = window.RTCPeerConnection ||
        window.mozRTCPeerConnection ||
        window.webkitRTCPeerConnection

      console.log('PeerConnection', PeerConnection)
      this.peerA = new PeerConnection(this.getIceServers())
      this.peerB = new PeerConnection(this.getIceServers())
      this.peerA.addStream(this.localstream)
      this.peerA.onicecandidate = (event) => {
        //  A ICE  B 
        if (event.candidate) {
          this.peerB.addIceCandidate(event.candidate)
        }
        this.call()
      }
      this.peerB.onaddstream = (event) => {
        const video = this.$refs.videoB
        video.srcObject = event.stream
      }
      this.peerB.onicecandidate = (event) => {
        if (event.candidate) {
          this.peerA.addIceCandidate(event.candidate)
        }
      }
    },
    async call () {
      const offer = await this.peerA.createOffer(this.offerOption)
      await this.onCreateOffer(offer)
    },
    // offer
    async onCreateOffer (desc) {
      try {
        await this.peerB.setLocalDescription(desc) //  offer 
        await this.peerA.setRemoteDescription(desc) //  offer 
        await this.onCreateAnswer()
      } catch (err) {
        console.error(err)
      }
    },
    // answer
    async onCreateAnswer () {
      try {
        const answer = await this.peerA.createAnswer() //  answer
        await this.peerA.setLocalDescription(answer) //  answer 
        await this.peerB.setRemoteDescription(answer) //  answer 
      } catch (err) {
        console.error(err)
      }
    },
    // iceServers
    getIceServers () {
      return {
        iceServers: [
          { url: 'stun:stun.l.google.com:19302' } // 
        ]
      }
    }
  }
}
</script>

<style lang='scss' scoped>
#rtcA,
#rtcB {
  width: 100%;
  margin-top: 10px;
  height: 300px;
  display: block;
  background-color: #eee;
}
</style>
